.skillsBlock{
    height: 100vh;
    background-color: #d7f3bc;
    padding: 50px 0;
}

.skillsContainer{
    flex-direction: column;
    justify-content: flex-start;
    flex-wrap: nowrap;
}

.skills {
    border: 1px solid;
    width: 100%;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
}

.title {
    background-color: #bfbdbd;
}
